import Box from "./Box";
import ph1 from "./photos/html.png";
import ph2 from "./photos/css-3.png";
import ph3 from "./photos/js.png";
import ph4 from "./photos/atom.png";
import ph6 from "./photos/typescript.png";
import ph7 from "./photos/sasslogo.png";
import Html from "./Html";
import Css from "./Css";
import Java from "./Java";
import React from "./React";
import Type from "./Type";
import Sass from "./Sass";

export default function Skills() {
  window.onresize = () => {
    document.querySelectorAll(".skillsDet").forEach((e) => {
      e.classList.remove("active2");
    });
  };
  return (
    <div className="skills li">
      <Box
        clsss={"htmldetails"}
        class2={"html"}
        photo={ph1}
        persantege={"95"}
        title={"Hypertext Markup Language (Html)"}
      >
        <p className="discripeP">
          learn most popular tags in html and propertys like classes and id and
          learn to build that tages corectly it's the basic of front-end
          devloper and the easiest one
        </p>
      </Box>
      <div className="htmldetails2 skillsDet">
        <Html />
      </div>
      <Box
        clsss={"cssdetails"}
        class2={"css"}
        photo={ph2}
        persantege={"90"}
        title={"Cascading Style Sheets (css)"}
      >
        <p className="discripeP">
          learn all most important property of styling and mange the position of
          all element and learn a lot about animation and moudul and make a
          website responsive for all devices
        </p>
      </Box>
      <div className="cssdetails2 skillsDet">
        <Css />
      </div>
      <Box
        clsss={"javadetails"}
        class2={"java"}
        photo={ph3}
        persantege={"85"}
        title={"javascript"}
      >
        <p className="discripeP">
          it's the first programing language the front-end will learn and it's
          vary importan for the website it's make the website Dynamic and More
          interactive
        </p>
      </Box>
      <div className="javadetails2 skillsDet">
        <Java />
      </div>
      <Box
        clsss={"reactdetails"}
        class2={"react"}
        photo={ph4}
        persantege={"85"}
        title={"react.js"}
      >
        <p className="discripeP">
          react framwork it's a tecnologey that make javascript easier by using
          JSX , component and librarys
        </p>
      </Box>
      <div className="reactdetails2 skillsDet">
        <React />
      </div>

      <Box
        clsss={"typedetails"}
        class2={"type"}
        photo={ph6}
        persantege={"85"}
        title={"typescript"}
      >
        <p className="discripeP">
          tpyescript is a language based on javascript but with type for
          variable and function
        </p>
      </Box>
      <div className="typedetails2 skillsDet">
        <Type />
      </div>
      <Box
        clsss={"sassdetails"}
        class2={"sass"}
        photo={ph7}
        persantege={"90"}
        title={"sass"}
      >
        <p className="discripeP">
          sass is a preprossesor for css it's append loops, condtions and
          varibles for css to be more dynamic
        </p>
      </Box>
      <div className="sassdetails2 skillsDet">
        <Sass />
      </div>
    </div>
  );
}
