import { useEffect, useRef } from "react";
import Tilt from "vanilla-tilt";

export default function Box({
  photo,
  persantege,
  title,
  children,
  clsss,
  class2,
}) {
  let tiltref = useRef(null);
  useEffect(() => {
    Tilt.init(tiltref.current, {
      scale: 1,
      speed: 100,
      max: 5,
    });
  }, []);

  return (
    <>
      <div className={`box ${class2}`} ref={tiltref}>
        <div className="progres">
          <img
            style={{ width: "60px", borderRadius: "50%" }}
            src={photo}
            alt=""
            loading="lazy"
          ></img>
          <div className="pro">
            <div style={{ width: `${persantege}%` }}>
              <span style={{ backgroundColor: `var(--color6)` }}></span>
            </div>
          </div>
          <span
            style={{
              color: "white",
              fontSize: "20px",
              letterSpacing: "1px",
            }}
          >
            {persantege}%
          </span>
        </div>
        <div className="discripe">
          <p
            style={{
              fontSize: "20px",
              color: "white",
              letterSpacing: "2px",
              fontWeight: "bold",
              marginBottom: "25px",
            }}
          >
            {title}
          </p>
          {children}
        </div>
        <div className="details">
          <p
            onClick={() => {
              if (window.innerWidth > 870) {
                document.querySelectorAll(".sidedetails").forEach((e) => {
                  e.style.display = "none";
                });
                document.querySelectorAll(".sidedetails .tags").forEach((e) => {
                  e.classList.remove("active2");
                });
                document.querySelector(`.${clsss}`).style.display = "block";
                document
                  .querySelector(`.${clsss} .tags`)
                  .classList.add("active2");
              } else {
                document.querySelectorAll(".skillsDet").forEach((e) => {
                  e.classList.remove("active2");
                });
                document.querySelectorAll(".skillsDet .tags").forEach((e) => {
                  e.classList.remove("active2");
                });
                document.querySelector(`.${clsss}2`).classList.add("active2");
                document
                  .querySelector(`.${clsss}2 .tags`)
                  .classList.add("active2");
              }
            }}
          >
            click to see more detalis or projects{" "}
            <i
              style={{ fontSize: "20px" }}
              className="fa-solid fa-arrow-right-long"
            ></i>
          </p>
        </div>
      </div>
    </>
  );
}
